//fix for mysql trailing newline deletion problem
function evenUpSizes(a, b) {
  if (a.length > b.length) {
    while (a.length > b.length) b.push("");
  } else if (b.length > a.length) {
    while (b.length > a.length) a.push("");
  }
}
